<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/product/format/list" type='list' @click="rest" >
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="名称" prop="name" :error='errorMessage.name'>
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label="位置" prop="position" :error='errorMessage.position'>
          <el-radio-group v-model="formData.position">
            <el-radio :label="1">顶部</el-radio>
            <el-radio :label="2">底部</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="版式内容" prop="content" :error='errorMessage.content' class="default">
          <cms-editor v-model="formData.content"></cms-editor>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update()" v-color v-if="type=='update'">保存修改</cms-button>
         <cms-button @click="save(false)" v-color v-if="type=='save'">保存并新增</cms-button>
          <cms-button @click="save(true)" v-color v-if="type=='save'">保存</cms-button>
        <cms-button type='rest' @click="restData">重置</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import rules from './../../../../rules/index.js'
import form from '@/mixins/form.js'
import {
  productFormatUpdate,
  productFormatSave,
  productFormatGet
} from './../../../../api/product/product.js'

var defaultData = {
  position: 2,
  name: '',
  content: ''

}
export default {
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      rules: {
        position: [rules.required()],
        name: [rules.required()],
        content: [rules.required()]
      }
    }
  },

  methods: {
    getFormData(id) {
      productFormatGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
        }
      })
    },
    save(state) {
      this.saveForm(productFormatSave, this.formData, state, '/product/format/list', 'list')
    },
    update() {
      this.updateForm(productFormatUpdate, this.formData, '/product/format/list', 'list')
    },
    restData() {
      if (this.$route.query.type === 'update') {
        this.getFormData(this.$route.query.id)
      } else {
        this.rest()
      }
    }
  },
  created() {
    if (this.$route.query.type === 'update') {
      this.getFormData(this.$route.query.id)
    }
  }
}
</script>

<style>
</style>



